"use client";

import Link from "next/link";
import { useMemo, useState } from "react";
import { useParams } from "next/navigation";
import { usePatientDetails } from "@/hooks/use-patients";
import { usePatientProtocols } from "@/hooks/use-protocols";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { EmptyState, ErrorState, LoadingState, SuccessState } from "@/components/shared/data-states";
import type { ProtocolModuleType } from "@/types/protocol";

const allModules: ProtocolModuleType[] = ["nutrition", "training", "supplementation", "hydration"];

export default function PatientProtocolPage() {
  const params = useParams<{ id: string }>();
  const patientId = params.id;

  const patient = usePatientDetails(patientId);
  const protocols = usePatientProtocols(patientId);
  const [saveMessage, setSaveMessage] = useState<string | null>(null);

  const activeProtocol = useMemo(
    () => (protocols.data ?? []).find((protocol) => protocol.status === "active") ?? null,
    [protocols.data],
  );

  const [selectedModules, setSelectedModules] = useState<ProtocolModuleType[]>(activeProtocol?.modules ?? ["nutrition", "training"]);

  if (patient.loading || protocols.loading) {
    return <LoadingState label="Carregando editor de protocolo" />;
  }

  if (patient.error || protocols.error) {
    return (
      <ErrorState
        description="Não foi possível carregar o protocolo do paciente."
        onRetry={() => {
          void patient.reload();
          void protocols.reload();
        }}
      />
    );
  }

  if (!patient.data) {
    return (
      <EmptyState
        title="Paciente não encontrado"
        description="Não foi possível carregar os dados para edição de protocolo."
      />
    );
  }

  const toggleModule = (module: ProtocolModuleType) => {
    setSelectedModules((previous) =>
      previous.includes(module) ? previous.filter((item) => item !== module) : [...previous, module],
    );
  };

  return (
    <main className="mx-auto flex w-full max-w-5xl flex-col gap-5">
      <header className="kv-surface rounded-3xl p-6">
        <p className="kv-pill inline-flex">PRO-07</p>
        <h1 className="mt-3 text-2xl font-semibold tracking-tight">Protocolo Integrado (Editor)</h1>
        <p className="mt-2 text-sm text-muted-foreground">Paciente: {patient.data.name}</p>
      </header>

      {saveMessage ? <SuccessState message={saveMessage} /> : null}

      <Card>
        <CardHeader>
          <CardTitle>Configuração do protocolo</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <p className="text-sm text-muted-foreground">
            Protocolo atual: {activeProtocol?.name ?? "Sem protocolo ativo"}
            {activeProtocol ? ` • versão ${activeProtocol.version}` : ""}
          </p>

          <div className="grid gap-2 md:grid-cols-2">
            {allModules.map((module) => (
              <label key={module} className="flex items-center gap-2 rounded-xl border border-border/70 px-3 py-2 text-sm capitalize">
                <input
                  type="checkbox"
                  checked={selectedModules.includes(module)}
                  onChange={() => toggleModule(module)}
                />
                {module}
              </label>
            ))}
          </div>

          <div className="flex flex-wrap gap-2">
            <Button
              variant="outline"
              onClick={async () => {
                await new Promise((resolve) => setTimeout(resolve, 300));
                setSaveMessage("Rascunho salvo com sucesso.");
              }}
            >
              Salvar rascunho
            </Button>
            <Button
              onClick={async () => {
                await new Promise((resolve) => setTimeout(resolve, 300));
                setSaveMessage("Protocolo ativado para o paciente com sucesso.");
              }}
              disabled={selectedModules.length === 0}
            >
              Ativar protocolo
            </Button>
            <Link href="/protocols/proto_001">
              <Button variant="ghost">Abrir visão detalhada</Button>
            </Link>
          </div>
        </CardContent>
      </Card>
    </main>
  );
}
